  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5715;
}

h1, h2, h3, h4, h5, h6 {
  color: #000000d9;
  font-weight: 500;
  margin-bottom: .5em;
  margin-top: 0;
}

.center-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  margin: 0;
}

.main-shadow {
  border: 1px solid #4d50551a;
  border-top-left-radius: 8px;
  border-radius: 8px;
  box-shadow: 0 1px 8px 0 #00000014;
  position: relative;
  z-index: 1;
}

.omd-title-h2 {
  color: #1a1a1a;
  font-size: 28px;
  font-weight: 700;
  line-height: 32px;
  margin: 0 0 24px;
  width: 300px;
}

.img-auth {
  background-color: #fff8e8;
  border-radius: 50%;
}

.auth-name {
  color: #1a1a1a9c;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  margin: 0;
}

.minute {
  align-items: center;
  display: flex;
  gap: 8px;
}

.minute svg {
  color: #00a2ff;
  height: 22px;
  width: 22px;
}

.minute span {
  color: #1a1a1a9c;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between; 
  padding-left: 5px;
  padding-right: 5px;
}

.nav-body {
  height: 400px;
}


.footer__wrapper p:first-child {
  color: #0069ff;
  font-size: 14px;
  font-weight: 400;
}

.footer__wrapper p:last-child {
  color: #1a1a1a;
  font-size: 14px;
  font-weight: 400;
}

.logo__wrapper-calendly {
  height: 105px;
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  right: -5px;
  text-decoration: none;
  top: -5px;
  transition: all .1s ease-in;
  width: 105px;
  will-change: transform;
  z-index: 1;
}

.logo__wrapper-calendly .background {
  background-color: #505960;
  box-shadow: 0 2px 4px 0 #0003;
  color: #fff;
  display: block;
  font-weight: 700;
  left: -11px;
  line-height: 1.2;
  padding: 9px 0 6px;
  pointer-events: auto;
  position: relative;
  text-align: center;
  text-decoration: none;
  top: 21px;
  transform: rotate(45deg);
  transition: all .1s ease-in;
  width: 160px;
  z-index: 1;
}

.logo__wrapper-calendly .powerd__by {
  font-size: 8px;
}

.logo__wrapper-calendly .calendly {
  font-size: 14px;
}

.form__wrapper.date__time {
  width: 100%;
}

.date__time {
  border: 1px solid #4d50551a;
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
  box-shadow: 0 1px 8px 0 #00000014;
  padding: 28px 2px 0 19px;
  z-index: 1;
}

.date__time h2 {
  color: #1a1a1a;
  font-size: 20px;
  font-weight: 700;
  line-height: 30px;
}

#msform {
  margin-top: 20px;
  position: relative;
  text-align: center;
}

#progressbar {
  color: #d3d3d3;
  margin-bottom: 30px;
  overflow: hidden;
  padding: 0;
}

#progressbar .active {
  color: #00a2ff;
}

*, :after, :before {
  box-sizing: border-box;
}

#progressbar li {
  float: left;
  font-size: 15px;
  font-weight: 400;
  list-style-type: none;
  position: relative;
  width: 33.33333%;
}

#progressbar #account:before {
  content: "\f007";
  font-family: FontAwesome;
}

#progressbar li.active:after, #progressbar li.active:before {
  background: #00a2ff;
}

#progressbar li:before {
  background: #d3d3d3;
  border-radius: 50%;
  color: #fff;
  display: block;
  font-size: 20px;
  height: 50px;
  line-height: 45px;
  margin: 0 auto 10px;
  padding: 2px;
  width: 50px;
}

#progressbar li:after {
  background: #d3d3d3;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  top: 25px;
  width: 100%;
  z-index: -1;
}

::selection {
  background: #1890ff;
  color: #fff;
}

#progressbar #personal:before {
  content: "\f073";
  font-family: FontAwesome;
}

#progressbar #confirm:before {
  content: "\f00c";
  font-family: FontAwesome;
}

