/* Loading Confirm */

.finalStep, .step1, .step2, .step3, .step4 {
  opacity: 0;
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

.loading-container {
  background-color: #fff;
  padding: 30px;
  text-align: center;
}

.loading-message {
  font-size: 20px;
  margin-bottom: 20px;
}

.step1 {
  animation: fadeIn 1s 1s forwards, fadeOut 1s 5s forwards;
}

.loading-submessage {
  font-size: 16px;
  margin-bottom: 10px;
}

.step2 {
  animation: fadeIn 1s 2s forwards, fadeOut 1s 5s forwards;
}

.step3 {
  animation: fadeIn 1s 3s forwards, fadeOut 1s 5s forwards;
}

.step4 {
  animation: fadeIn 1s 4s forwards, fadeOut 1s 5s forwards;
}

.finalStep {
  animation: fadeIn 1s 6s forwards;
}

.loader {
  height: 30px;
  margin: 40px auto;
  position: relative;
  width: 30px;
}

.loader {
  animation: rotation 1s linear infinite;
  border: 2px solid;
  border-color: #fff #fff #0000;
  border-radius: 50%;
  box-sizing: border-box;
  display: inline-block;
  height: 28px;
  width: 28px;
}

.loader:after, .loader:before {
  animation: spin 1s infinite;
  border: 6px solid #0000;
  border-radius: 50%;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.loader:before {
  animation-delay: .5s;
  border-top-color: #1877f2;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

